import { Subject } from 'rxjs';
/**
 * Creates dynamic output for property
 */

export function DynamicOutput() {
  return function (target, propertyKey) {
    const prop = propertyKey;
    Object.defineProperty(target, `${prop}Change`, {
      get: function () {
        if (!this[`ɵ${prop}Change`]) {
          this[`ɵ${prop}Change`] = new Subject();
        }

        return this[`ɵ${prop}Change`];
      }
    });
    Object.defineProperty(target, prop, {
      get: function () {
        return this[`ɵ${prop}`];
      },
      set: function (value) {
        this[`ɵ${prop}`] = value;
        this[`${prop}Change`].next();
      }
    });
  };
}